.app-list-item {
  font-size: 1.25rem;
}

.app-list-item button {
  width: 35px;
  height: 35px;
  margin: 3px;
  font-size: 17px;
  border: none;
  cursor: pointer;
}
.app-list-item button:focus {
  box-shadow: none;
  outline: none;
}
.app-list-item .btn-star {
  color: #fc0000;
}
.app-list-item .btn-trash {
  color: red;
}
.app-list-item .fa.bi-heart-fill {
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  font-size: 16px;
  color: red;
  transition: 0.3s all;
  transform: translateX(30px);
  opacity: 0;
}

.app-list-item-label {
  display: block;
  line-height: 35px;
  cursor: pointer;
  user-select: none;
  transition: 0.5s all;
}

.app-list-item-label:hover {
  transform: scale(1.2, 1.2);
}

.app-list-item.like .bi-heart-fill {
  opacity: 1;
  transform: translateX(0px);
  transform: scale(2, 2);
}

.app-list-item.important .app-list-item-label {
  color: #8400ff;
}

.app-list-item.important .btn-star {
  color: #8400ff;
  transition: 0.5s all;
}

.app-list-item.important .btn-star:hover {
  transform: scale(1.2, 1.2);
}

.app-list-item .btn-star {
  transition: 0.5s all;
}

.app-list-item .btn-star:hover {
  transform: scale(1.2, 1.2);
}
